
/* Quando si passa il mouse sulla card, il logo diventa visibile */
.img-service:hover .bg-image {
    opacity: 1;
  }
  
  /* Immagine del membro: posizione relativa per stare sopra il logo */
  .member-image {
    position: relative;
    z-index: 2;
  }
  
  .image-overlay {
    z-index: 3;
  }
  
  .card-img-overlay::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0); /* inizialmente trasparente */
    transition: background 0.3s ease;
    z-index: 1;
  }
  
  /* Assicurati che il contenuto (testo, bottoni, ecc.) stia sopra l'overlay */
  .card-img-overlay > * {
    position: relative;
    z-index: 2;
  }

  /* Imposta il contenitore come contesto di posizionamento */
.img-service {
  position: relative;
}

/* Immagine del logo: posizionata assolutamente e sotto l'immagine removebg */
.bg-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

/* Immagine removebg: posizionata in primo piano */
.member-image {
  position: relative;
  z-index: 2;
}
  
.clickable {
  cursor: pointer;
}